import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import logo from "../pages/images/codeit-icon.png"

const Header = ({ siteTitle }) => (
  <header>
    <nav class="navbar navbar-expand-lg navbar-dark bg-dark fixed-top">
      <div class="container">
        <Link to="/">
          <div class="navbar-brand">
            <img src={logo} height="42" alt=""></img> {siteTitle}
          </div>
        </Link>

        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
        <div class="collapse navbar-collapse" id="navbarResponsive">
          <ul class="navbar-nav mr-auto">
            <li class="nav-item" id="content-desktop">
              <Link to="/about">
                <a href class="nav-link">
                  عنــا <i class="far fa-address-card"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item active" id="content-mobile">
              <Link to="/">
                <a href class="nav-link">
                  الرئيسية <i class="fas fa-home"></i>
                </a>
              </Link>
            </li>

            <li class="nav-item" id="content-desktop">
              <Link to="/blog">
                <a href class="nav-link">
                  المدونة <i class="fas fa-blog"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item" id="content-mobile">
              <Link to="/category">
                <a href class="nav-link">
                  الأقسام <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>

            <li class="nav-item" id="content-desktop">
              <Link to="/contact">
                <a href class="nav-link">
                  تواصل معنا <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item" id="content-mobile">
              <Link to="/tags">
                <a href class="nav-link">
                  الوسوم <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>

            <li class="nav-item" id="content-desktop">
              <Link to="/tags">
                <a href class="nav-link">
                  الوسوم <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item" id="content-mobile">
              <Link to="/contact">
                <a href class="nav-link">
                  تواصل معنا <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>

            <li class="nav-item" id="content-desktop">
              <Link to="/category">
                <a href class="nav-link">
                  الأقسام <i class="fas fa-mail-bulk"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item" id="content-mobile">
              <Link to="/blog">
                <a href class="nav-link">
                  المدونة <i class="fas fa-blog"></i>
                </a>
              </Link>
            </li>

            <li class="nav-item active" id="content-desktop">
              <Link to="/">
                <a href class="nav-link">
                  الرئيسية <i class="fas fa-home"></i>
                </a>
              </Link>
            </li>
            <li class="nav-item" id="content-mobile">
              <Link to="/about">
                <a href class="nav-link">
                  عنــا <i class="far fa-address-card"></i>
                </a>
              </Link>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: ``,
}

export default Header
